import React, {useState} from 'react';
import { Modal, Image } from 'react-bootstrap';
import img1 from "../guides/1/main.png";
import {TEXTS} from "../js/text";

const ImageModal = ({ imgSrc, alt }) => {
    const [show, setShow] = useState(false);
    const onHide = () => {
        setShow(false);
    }
    const openModal = () => {
        setShow(true);
    }
    return (
        <div>
            <Image
                src={imgSrc}
                alt={alt}
                style={{cursor: 'pointer'}}
                onClick={openModal}
                rounded
                fluid
            />
            <Modal show={show} onHide={onHide} centered>
                <Modal.Header closeButton>
                    <Modal.Title>{TEXTS.VIEW_IMAGE}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Image
                        src={imgSrc}
                        alt=" "
                        fluid
                    />
                </Modal.Body>
            </Modal>
        </div>
    );
};

export default ImageModal;
