import React from 'react';
import MyNavbar from "../components/MyNavbar";
import {Button, Card, Container} from "react-bootstrap";
import {TEXTS} from "../js/text";
import {PRACTICE_GUIDES} from "../js/refs";
import {useNavigate} from "react-router-dom";

const Instructions = () => {
    const navigate = useNavigate();
    const nav = guideId => {
        navigate(`/showguide/${guideId}`);
    };

    return (
        <Container className="mt-3 text-center">
            <MyNavbar />
            <Card
                className="shadow-lg mt-5 d-flex flex-column justify-content-center"
                border="secondary"
            >
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.INSTRUCTIONS}</h1>
                    </Card.Title>
                    <Container className="mt-5">
                        <Card className="shadow-sm p-3 bg-light rounded-3">
                            <Card.Body>
                                {PRACTICE_GUIDES.map(guide =>
                                    <Card key={guide.id} className="mt-3">
                                        <Card.Body>
                                            <Card.Title>
                                                <h2 className="fs-3">
                                                    {guide.TITLE}
                                                </h2>
                                            </Card.Title>
                                            <p className="fs-5">
                                                {guide.DESCRIPTION}
                                            </p>
                                            <Button
                                                className="mt-2"
                                                onClick={() => nav(guide.id)}
                                                title={TEXTS.SHOW_GUIDE}
                                            >
                                                
                                            </Button>
                                        </Card.Body>
                                    </Card>
                                )}
                            </Card.Body>
                        </Card>
                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default Instructions;
