import React from 'react';
import MyNavbar from "../components/MyNavbar";
import {Card, Container} from "react-bootstrap";
import {TEXTS} from "../js/text";

const FAQ = () => {
    return (
        <Container className="mt-3 text-center" fluid>
            <MyNavbar />
            <Card className="shadow-lg mt-5" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.FAQ}</h1>
                    </Card.Title>
                    <Container className="mt-5">

                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default FAQ;