import React, { useEffect, useState } from 'react';
import { db, collection, getDocs, addDoc } from '../js/firebase';
import { getOrgs } from "../js/utils";
import useFetching from "../hooks/useFetching";
import { Alert, Card, Container, Form, Table, Button } from "react-bootstrap";
import { TEXTS } from "../js/text";
import MOrgDisplay from "./MOrgDisplay";
import { KEYS_DESCRIPTIONS, MIN_ORG_LENGTH_FOR_SEARCH } from "../js/refs";
import MAddOrgModal from './MAddOrgModal';
import MOrgLogic from "./MOrgLogic";

function MOrgsList() {
    const [filteredOrgs, setFilteredOrgs] = useState([]);
    const [orgs, setOrgs] = useState([]);
    const [searchOrgName, setSearchOrgName] = useState('');
    const [showAddModal, setShowAddModal] = useState(false);

    const [fetchOrgs, isLoading, error, statusMessage] = useFetching(async () => {
        const orgs = await getOrgs();
        setOrgs(orgs);
        setFilteredOrgs(orgs);
    });

    useEffect(() => {
        fetchOrgs();
    }, []);

    useEffect(() => {
        if (searchOrgName.length > MIN_ORG_LENGTH_FOR_SEARCH) {
            setFilteredOrgs(orgs.filter(org =>
                org.orgName.toLowerCase().includes(searchOrgName.toLowerCase())
            ));
        }
        if (searchOrgName.length === 0) {
            setFilteredOrgs([...orgs]);
        }
    }, [searchOrgName, setSearchOrgName]);

    const onOrgRemoved = orgId => {
        setOrgs(orgs.filter(org => org.id !== orgId));
    };

    const onOrgAdded = newOrg => {
        setOrgs([...orgs, newOrg]);
        setFilteredOrgs([...filteredOrgs, newOrg]);
    };

    return (
        <Container className="mt-3 text-center d-flex align-items-center justify-content-center">
            <Card className="shadow-lg" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.ORGS}</h1>
                    </Card.Title>
                    <Button
                        variant="success"
                        className="mb-3"
                        onClick={() => setShowAddModal(true)} //  
                    >
                        {TEXTS.ADD_ORG}
                    </Button>
                    <Container className="mt-5">
                        {isLoading && statusMessage}
                        {error && error}
                        {!isLoading && !error && statusMessage && statusMessage}
                    </Container>
                    <Table striped bordered hover responsive>
                        <thead>
                        <tr>
                            <th></th>
                            <th>
                                {KEYS_DESCRIPTIONS.orgName}
                                <Form.Control
                                    type="text"
                                    placeholder={TEXTS.SEARCH}
                                    value={searchOrgName}
                                    onChange={e => setSearchOrgName(e.target.value)}
                                />
                            </th>
                            <th>{KEYS_DESCRIPTIONS.agreementStartDate}</th>
                            <th>{KEYS_DESCRIPTIONS.agreementEndDate}</th>
                            <th>{KEYS_DESCRIPTIONS.practicalTrainingAgreementNumber}</th>
                            <th>{KEYS_DESCRIPTIONS.practicalTrainingContract}</th>
                            <th>{KEYS_DESCRIPTIONS.admissionDirections}</th>
                            <th>{KEYS_DESCRIPTIONS.isHidden}</th>
                        </tr>
                        </thead>
                        <tbody>
                        {filteredOrgs.map((org, index) => (
                            <MOrgLogic
                                key={org.id}
                                orgInfo={org}
                                i={index}
                                onOrgRemoved={onOrgRemoved}
                            />
                        ))}
                        {filteredOrgs.length === 0 &&
                            <tr>
                                <td colSpan={11}>
                                    {TEXTS.ORGS_NOT_FOUND}
                                </td>
                            </tr>
                        }
                        </tbody>
                    </Table>
                </Card.Body>
            </Card>
            <MAddOrgModal
                show={showAddModal}
                handleClose={() => setShowAddModal(false)}
                onOrgAdded={onOrgAdded}
            />
        </Container>
    );
}

export default MOrgsList;
